import Container from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";

const AuthorPage = () => (
  <Container>
    <Header title="O autorze" />

    <Section
      title="Krzysztof Worwa"
      body={<>
          <strong> Jest to moja pierwsza strona internetowa.</strong> Postaram
          si w niej przewiczy <em>jak najwicej</em> aspektw, ktre pomog mi
          tworzyc <em>profesjonalne</em> strony internetowe. <br /> Dodatkowo zajmuj si <b>Grafik 3d</b>,
          <b>tworzeniem wizualizacji</b> oraz <b>grafik reklamowych</b>
      </>}
    />
  </Container>
);

export default AuthorPage;